.navbar {
  background: #f8f9fa;
  box-shadow: 0 2px 4px #0000000d;
}

.navbar .logo img {
  filter: none;
}

.navbar .nav-link {
  color: #333;
}

.navbar .btn-get-started {
  color: #fff;
  background: #4caf50;
  border: none;
}

.navbar .btn-get-started:hover {
  background: #45a049;
}

.consultation-main {
  background: #f8f9fa;
  min-height: calc(100vh - 80px);
  padding-top: 80px;
}

.consultation-hero {
  grid-template-columns: 1fr 1fr;
  align-items: start;
  gap: 4rem;
  max-width: 1200px;
  margin: 0 auto;
  padding: 4rem 2rem;
  display: grid;
}

.consultation-content {
  max-width: 500px;
}

.consultation-content h1 {
  color: #333;
  margin-bottom: 1.5rem;
  font-size: 2.5rem;
}

.consultation-content p {
  color: #666;
  margin-bottom: 2rem;
  font-size: 1.1rem;
  line-height: 1.6;
}

.consultation-benefits {
  flex-direction: column;
  gap: 1rem;
  display: flex;
}

.benefit-item {
  color: #333;
  align-items: center;
  gap: 1rem;
  display: flex;
}

.benefit-item svg {
  color: #4caf50;
  flex-shrink: 0;
}

.consultation-calendar {
  background: #fff;
  border-radius: 12px;
  overflow: hidden;
  box-shadow: 0 4px 6px #0000001a;
}

.calendar-header {
  background: #f8f9fa;
  border-bottom: 1px solid #eee;
  align-items: center;
  gap: 1rem;
  padding: 1.5rem;
  display: flex;
}

.meet-icon {
  height: 24px;
}

.calendar-frame {
  height: 600px;
}

@media (width <= 1024px) {
  .consultation-hero {
    grid-template-columns: 1fr;
    gap: 2rem;
  }

  .consultation-content {
    text-align: center;
    max-width: 100%;
  }

  .consultation-benefits {
    align-items: center;
  }
}

@media (width <= 768px) {
  .consultation-hero {
    padding: 2rem 1rem;
  }

  .consultation-content h1 {
    font-size: 2rem;
  }

  .calendar-frame {
    height: 500px;
  }
}
